@import url('https://fonts.googleapis.com/css2?family=Grand+Hotel&family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
html, * {
  margin:0;
  padding:0;
  box-sizing:border-box;
  font-family: 'Montserrat', sans-serif;
  position:relative;
}
html{
  background-color: #333;
}

a{
  text-decoration: none;
  color: #fff;
}

ul, ul li{
  display: inline-block;
  list-style: none;
}

h1,h2,h3{
  font-family: 'Grand Hotel', cursive;
  color:#ffc200;
  font-size: 7vw;
  font-weight: normal;
}

h2{
 font-size: 5vw;
 color: #333;
}

h3{
  font-size: 2.5vw;
  color: #333;
}

h5{
  font-weight: bold;
  font-size: 1.5vw;
}

button{
  background-color: #ffc200;
  color: #333;
  border-radius: 5%;
  padding: 1vw 4vw;
  font-size: 1.4vw;
  cursor: pointer;
  font-weight: bold;
}

button:hover{
  background-color: #333;
  color:#ffc200;
  border: 2px solid #ffc200;
}

.App{
  width: 100%;
  margin:auto;
  /*background: url('img/bg.jpg') no-repeat center 0;
  height: 10000px;*/
}

.AppWrap{
  width: 100%;
  display:flex;
  flex-direction: column;
  -webkit-align-items: center; 
  align-items: center; 
  justify-content: center;
}

.Content{
  width: 80%;
  margin: auto 0;
  background-color: #303030;
}

.logomenu, .logomenu2{
  display:flex;
  justify-content: center;
  align-items: center;
  position:fixed;
  top:0;
  width: 100%;
  margin: auto 0;
  z-index: 1000;
}
.logomenu2{
  background-color: #333;
}
.logomenuwrap{
  width: 80%;
  margin: auto 0;
  display:flex;
  justify-content: left;
}

.logo{
  flex:3;
  padding: 2.5% 3.6%;
}

.logo a img:hover{
  transform: scale(1.1);
}

.mainmenu{
  font-size:1.5em;
  color:#fff;
  padding: 3.1% 0 3.1% 0;
  flex:15;
  text-align: right;
  overflow: hidden;
}

.mainmenu ul {
  list-style: none;
  display: flex;
  justify-content: right;
}

.mainmenu ul li{
  margin-left: 3.5vw;
}

.mainmenu ul li:last-child{
  margin-right: 2vw;
}

.mainmenu ul li a{

}

.mainmenu ul li:hover{
  border-bottom: 1px solid #fff;
  animation: smallToBig 500ms ease;
    z-index:999;   
    transform: scale(1.1)
}

.search{
  flex: 1;
  padding: 3.1% 3.9% 3.1% 0;
  text-align: right;
  overflow:hidden;
}
.search img:hover{
  transform: scale(1.1);
}



.header{
  width: 100%;
  display:flex;
  flex-direction: row;
  -webkit-align-items: stretch; 
  align-items: stretch; 
  justify-content: center;
  padding:0;
  z-index: 1;
}

.headerwrap{
  width: 100%;
  margin: auto 0;
  display:flex;
  justify-content: left;
}

.header-img{
  flex:1;
}
.header-img img{
  object-fit: cover;
}

.header-txt{
  flex:1;
  background-image: url('img/menubg.png');
  margin-left: -32.6vw;
  
}

.header-txt h1{
  padding: 10.5vw 0 0 13vw;
}

.header-txt p{
  font-size: 1.5vw;
  padding: 0 5vw 0 13vw;
  color: #fff;
  line-height: 2vw;
}

.header-txt button{
  background-color: #ffc200;
  color: #333;
  text-transform: uppercase;
  border-radius: 5px;
  font-size: 20px;
  padding: 10px 55px;
  margin:2.3vw 0 0 13vw;
  cursor: pointer;
}
.header-txt button:hover{
  transform: scale(1.1);
}

.header-menu{
  flex:1;
  /*background-image: url("img/menubg.png");*/
  background-size: cover;
  background-repeat: no-repeat;
  margin-left: -13%;
  z-index: 10;
}

.bestSeller{
  background-color: #fff;
  color: #333;
  text-align: center;
}
.bestSeller h2{
  padding: 6.5vw 0 0 0;
}
.bestSeller p {
  font-size: 1.5vw;
  padding: 1.1vw 13vw 0 13vw;
  line-height: 1.6vw;
  
}
.features{
  width: 100%;
  display: flex;
  text-align: center;
  flex-direction: row;
  align-items: stretch;
  padding: 3vw 0 6vw 0;
}
.feature{
  flex:1;
}
.feature img{
  border-radius: 50%;
}

.feature p {
  margin:0;
  padding:0;
  font-size: 1.1vw;
  padding: 0 3vw ;
  line-height: 1.3vw;
  text-align: center;
}

.feature button{
  background-color: #333;
  color: #ffc200;
  text-transform: uppercase;
  border-radius: 5px;
  font-size: 20px;
  padding: 10px 55px;
  cursor: pointer;
  margin: 1.3vw 0;
  display:none;
}

.feature button:hover{
  color:#333;
  background-color:#ffc200;
}

.stats{
  padding: 4.0vw 0 0 0;
}
.stats h2{
  color:#ffc200;
}
.stats p{
  font-size: 1.5vw;
  line-height: 1.6vw;
  color: #fff;
}

.statsTop{
  background-color: #fff;
  background-image: url('img/statsTop.png');
  margin: -4vw 0 0 0;
  display:flex;
  flex-direction: row;
}
.statsTopImg{
  flex:1;
  padding: 9.5vw 0 0 7vw;
}
.statsTopImg img{
  border-radius: 50%;
}
.statsTopTxt{
  flex:1;
  padding: 13.5vw 0 0 0;
  margin: 0 0 0 -3vw;
}

.statsTopTxt p{
  padding: 1.2vw 0 0 0;
}
.statsTopTxt button{
  background-color: #ffc200;
  color:#333;
  border-radius: 5%;
  padding: 1vw 4vw;
  font-size: 1.4vw;
  margin-top: 3vw;
  cursor:pointer;
  font-weight: bold;
}
.statsTopTxt button:hover{
  background-color: #333;
  color:#ffc200;
  border: 2px solid #ffc200;
}

.statsMid{
  background-color: #303030;
  background-image: url('img/statsMid.png');
  background-repeat: no-repeat;
  text-align: center;
  margin:4.8vw 0 0 0;
  padding: 2.8vw 0 10vw 0;
  opacity: 1.5;
}
.statsMid p{
  margin: 0.5vw 15.6vw 3.5vw 15.6vw;
}
.stats ul{
  
}
.statsMid li{
  border: 6px solid #fff;
  margin: 2vw;
  border-radius: 50%;
  width: 12.5vw;
  height: 12.5vw;
  overflow: hidden;
  text-align: center;
  padding: 3vw 0 0 0;
}
.statsMid li span.number{
  color:#ffc200;
  font-size: 3vw;
  font-weight: bold;
}
.statsMid li span.text{
  color: #fff;
  font-size: 1.7vw;
  font-weight: bold;
  padding: 1.5vw 0 0 0;
}

.Offer{
  padding: 5vw 0 15vw 7vw;
  display:flex;
  flex-direction: row;
  background-image: url(img/statsBottom.png);
  background-repeat: no-repeat;
  background-color: #fff;
}
.OfferLeft{
  padding-top: 1vw;
}
.Offer h2{
  color:#ffc200;
}
.Offer p{
  color:#fff;
  font-size: 1.8vw;
  width: 34vw;
}
.Offer button{
  margin: 3.2vw 0 0 0;
}
.OfferRight{
  padding: 0 0 0 5vw;
}
.Offer img{
  border-radius: 50%;
}

.Quote{
  background-image: url(img/quotesBG.jpg);
  text-align: center;
  padding: 10vw 13vw 13.7vw 13vw;
}
.Quote p{
  font-size: 2.5vw;
}
.Quote h2{
  padding-top: 1vw;
  font-weight: normal;
}

.Subscription{
  text-align: center;
  padding: 5vw 0 13.5vw 0;
  background-image: url(img/subscriptionBG.jpg);
  background-repeat: no-repeat;
}
.Subscription p {
  font-size: 1.6vw;
  padding: 0.5vw 15vw 4vw 15vw;
  color:#333;
}
.Subscription input{
  font-size: 1.5vw;
  padding: 10px 20px;
  width: 30vw;
  border: #fff;
  background-color: #fff;
}
.Subscription button{
  padding: 0;
  padding: 0.5vw 2.5vw 0.5vw 2.5vw;
  border-radius: 0;
  border:none;
}

.Footer{
  display: flex;
  flex-direction: row;
  background-color: #2f2f2f;
  color: #fff;
  padding: 13.8vw 5vw 20vw 5vw;
  font-size: 1.3vw;
}
.Footer a:hover{
  text-decoration: underline;
  font-size: 1.5vw;
}
.Footer ul{
  display: flex;
  flex-direction: column;
}
.Footer h5{
  padding-bottom: 1vw;
}
.Footer ul li{
  padding-bottom: 0.7vw;
}
.Footer .ftrTxt{
  flex: 3;
}
.Footer .ftrTxt p{
  padding: 0 8vw 2vw 0;
  line-height: 1.6vw;;
}
.Footer .ftrTxt ul{
  display: flex;
  flex-direction: row;
}
.Footer .ftrTxt ul li{
  padding: 0 0.5vw 0 0;
}
.Footer .ftrTxt img:hover{
  transform: scale(1.5);
}
.Footer .ftrAbout{
  flex: 1;
}
.Footer .ftrServices{
  flex: 1;
  padding: 0 2vw;
}
.Footer .ftrOther{
  flex: 1;
}